@import url("https://fonts.googleapis.com/css2?family=Darker+Grotesque:wght@400;700&display=swap");

.darker_font {
  font-family: "Darker Grotesque", sans-serif;
}
.input-field {
  border: 1px solid transparent; /* Ensure there's a border to change color on focus */
}

.input-field:focus {
  border-color: lightgray; /* Change to your desired border color */
  outline: none; /* Optional: Removes the default focus outline */
}
.chat_header {
  height: 4rem;
  background-color:  white;

  display: flex;
  padding: 0.5rem;
  align-items: center;
}
.header_image {
  height: 80%;
  margin-right: .5rem;
}

.text_block_outer {
  margin-top: 1rem;
}
.message_author {
  color: #244B45;
  margin-bottom: 0.25rem;
  font-size: smaller;
}
.chatbot_logo_click{
  width: 80px;
  height: 80px;

  overflow: hidden;
  position: fixed;
  cursor: pointer;
  right: 1.25rem;
  z-index: 99999;
  bottom: 1.25rem;
}
.chat_area {
  width: 400px;
  display: none;
  position: fixed;
  right: 24px;
  bottom: 120px;
  z-index: 999991;
  border-radius: 1rem;
  margin: auto;
 
  box-shadow: rgba(0, 0, 0, 0.5) 2px 2px 9px;
  border-radius: 1rem;
  overflow: hidden;
  
}
.input-field{

  width: 100%;
  margin-bottom: 0.25rem;
  color: black;
  background-color: transparent;
  font-size: 1rem;
  padding-left: 5px;
  font-family: Darker Grotesque, sans-serif;
  font-size: 16px;

}
.send_button{
  padding: 1rem;
  width: 8rem;
  color: black;
  background-color: transparent;
  font-size: 18px;
  border: none;
  font-family: Darker Grotesque, sans-serif;
  font-size: 16px;
  font-weight: bold;
}
.chat_input_container{
  display: flex;
  padding: 0.5rem;
  background-color: white;
  z-index: 900;

}
.chat-visible {
  display: block;
}
.message_content {
  background-color: white;
  padding: 0.5rem;
  border-radius: 0.5rem;
  text-align: left;
  display: inline-block;
  max-width: 80%;

  margin-right: 0%;
  box-shadow: rgba(0, 0, 0, 0.2) 2px 2px 9px;
}
.message_content a{
  color: #244B45;
  text-decoration: none;

}
.message_content a:hover{
  text-decoration: underline;
}
.header_buttons {
  position: absolute;
  right: 15px;
  top: 15px;
  font-size: small;
  color: gray;
  cursor: pointer;
}
.close-button{
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.shadow_field {
  box-shadow: rgba(0, 0, 0, 0.5) 2px 2px 9px;
  border-radius: 1rem;
  overflow: hidden;
}
.chat_name{
  font-weight: bold;
  color:#244B45;
}
.bg-chat-image {
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0.3;
  width: 100%;
  height: 100%;
  z-index: -1; /* Ensure it stays behind other content */
}
.loading_container img{
  width: 35px;
  height: 35px;
}
.bg-chat-image img {
  width: 100%;
  height: 100%;
  object-fit: cover; /* Cover the container without losing aspect ratio */
}

.chat_container {
  padding: 1.5rem;
  position: relative;

  max-width: 100%;
  font-family: 'Darker Grotesque', sans-serif;
  font-size: 16px;
  line-height: 120%;
  height: 18rem;
  overflow-y: auto;

}

@media (max-width: 600px) {
  .chat_area {
    width: 95%;
    height: 95%;
    right: 0;
    position: fixed;
    left: 0;
    bottom: 0;
    top: 0;
    background-color: white;
    display: flex;
    flex-direction: column;
  }
  .chatbot_logo_click{
    width: 60px;
    height: 60px;
  }
  .chat_container{
    height: 100%;
  }
  .inside_chat_parent {
    display: flex;
    height: 100%;
  }

  .shadow_field{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}
